import { useContext } from "preact/hooks"
import style from "./style"
import { route } from "preact-router"
import Container from "@material-ui/core/Container"
import Grid from "@material-ui/core/Grid"
import Avatar from "@material-ui/core/Avatar"
import Button from "@material-ui/core/Button"
import { makeStyles } from "@material-ui/core/styles"
import {
	Home as HomeIcon,
	LocalLibrary,
	Description,
	VideogameAsset,
	MenuBook,
} from "@material-ui/icons"
import UserContext from "../../context/UserContext"

const useStyles = makeStyles(theme => ({
	gridItem: {
		justifyContent: "center",
	},
	avatarHome: {
		backgroundColor: theme.palette.primary.light,
		height: "6em",
		width: "6em",
	},
	iconHome: {
		height: "4em",
		width: "4em",
		color: theme.palette.common.white,
	},
}))

function Home(props) {
	const classes = useStyles()

	const { user } = useContext(UserContext)

	const handleClickButton = (e, index) => {
		props.onSelect(index);
		switch (index) {
			case 1:
				route("/clases")
				break
			case 2:
				route("/comunicados")
				break
			case 3:
				route("/biblioteca")
				break
			case 4:
				route("/juegos")
				break
		}
	}

	return (
		<div class={style.home}>
			<Container>
				<h1 class={style.title}>
					<Avatar>
						<HomeIcon />
					</Avatar>
					<span>Inicio</span>
				</h1>
				<Grid container>
					<Grid item xs align="center">
						<Avatar className={classes.avatarHome}>
							<Button onClick={e => handleClickButton(e, 1)}>
								<LocalLibrary className={classes.iconHome} />
							</Button>
						</Avatar>
						<h3>Clases</h3>
					</Grid>
					<Grid item xs align="center">
						<Avatar className={classes.avatarHome}>
							<Button onClick={e => handleClickButton(e, 2)}>
								<Description className={classes.iconHome} />
							</Button>
						</Avatar>
						<h3>Comunicados</h3>
					</Grid>
					<Grid item xs align="center">
						<Avatar className={classes.avatarHome}>
							<Button onClick={e => handleClickButton(e, 3)}>
								<MenuBook className={classes.iconHome} />
							</Button>
						</Avatar>
						<h3>Biblioteca</h3>
					</Grid>
					<Grid item xs align="center">
						<Avatar className={classes.avatarHome}>
							<Button onClick={e => handleClickButton(e, 4)}>
								<VideogameAsset className={classes.iconHome} />
							</Button>
						</Avatar>
						<h3>Juegos</h3>
					</Grid>
				</Grid>
			</Container>
		</div>
	)
}

export default Home
